html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #f0f0f0;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin-inline: auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Roboto", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=titulo] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.notwebp.notavif .header__fondo {
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.2) 0%, rgba(195, 6, 6, 0.8) 100%), url(../img/mani_1.jpg);
}

.webp .header__fondo {
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.2) 0%, rgba(195, 6, 6, 0.8) 100%), url(../img/mani_1.webp);
}

.avif .header__fondo {
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.2) 0%, rgba(195, 6, 6, 0.8) 100%), url(../img/mani_1.avif);
}

.header__fondo {
  padding: 2rem 2rem 10rem 2rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top, 100%;
}
.header__barra {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to right, #c30606 0%, black 80%);
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-around;
  }
}
.header__logo {
  width: 10rem;
}
.header__contenido {
  text-align: center;
}
.header__titulo {
  color: #FFFFFF;
  font-size: 3rem;
}
@media (min-width: 480px) {
  .header__titulo {
    text-align: center;
    font-size: 4rem;
  }
}
@media (min-width: 768px) {
  .header__titulo {
    text-align: left;
    font-size: 5rem;
  }
}
.header__descripcion {
  color: #FFFFFF;
  font-weight: 400;
  position: relative;
  font-size: 4rem;
  color: #FFFFFF;
  font-size: 3.5rem;
}
.header__descripcion span {
  font-weight: 700;
  color: #fefefe;
  line-height: 1;
  text-transform: uppercase;
  display: block;
}
@media (min-width: 768px) {
  .header__descripcion {
    font-size: 6rem;
    text-align: center;
  }
}

.redes {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 2rem;
  gap: 5rem;
}
@media (min-width: 768px) {
  .redes__enlace {
    margin: 0;
  }
}
.redes__enlace-span {
  font-size: 0;
}
.redes__enlace-icono {
  background-color: #FFFFFF;
  color: #000;
  border-radius: 50%;
  padding: 1rem;
  width: 3rem;
  height: 3rem;
  transition: transform 0.2s;
}
.redes__enlace-icono:hover {
  transform: scale(1.2) rotate(45deg);
}

.navegacion {
  background-color: #262626;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    justify-content: space-around;
  }
}
.navegacion__enlace {
  font-family: "Roboto", cursive;
  color: #FFFFFF;
  font-size: 2rem;
  transition-property: transform;
  transition-duration: 0.3s;
  display: inline-block;
}
@media (min-width: 768px) {
  .navegacion__enlace:hover {
    color: #d9d9d9;
    transform: scale(1.2);
    text-decoration: underline;
    color: #ff6666;
  }
}

.contenido {
  margin-top: 5rem;
  padding: 0 5rem;
}
@media (min-width: 768px) {
  .contenido__principal {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
    align-items: flex-start;
  }
}
@media (min-width: 768px) {
  .contenido__imagen-lateral {
    height: 50rem;
    width: 100%;
    object-fit: cover;
  }
}
.contenido__descripcion {
  background-color: #FFFFFF;
  border-radius: 2rem;
  margin-top: 2rem;
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .contenido__descripcion {
    grid-column: 2/4;
    margin-top: 0;
  }
}
.contenido__texto {
  padding: 2rem;
  margin: 0;
  font-family: "Roboto", cursive;
  font-size: 2rem;
  text-align: left;
}
@media (min-width: 768px) {
  .contenido__texto {
    padding: 0 2rem;
  }
}
.contenido__boton {
  background-color: #c30606;
  padding: 1rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  border-radius: 1rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 90%;
  margin-left: 2rem;
}
@media (min-width: 768px) {
  .contenido__boton {
    width: 20rem;
  }
}
.contenido__boton:hover {
  background-color: #920404;
}

.blog__entrada {
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .blog__entrada {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.blog__texto {
  grid-column: 2/5;
}

.informacion {
  background-color: #eeefed;
  padding: 5rem;
}
.informacion__titulo {
  text-align: center;
  margin-bottom: 4rem;
}
.informacion__descargas {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .informacion__descargas {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.informacion__descarga {
  display: flex;
  align-items: center;
}
.informacion__icono {
  width: 5rem;
  height: 5rem;
  padding: 0 1rem;
  color: #fd3131;
}

.footer {
  margin-top: 5rem;
}
.footer__titulo {
  color: #FFFFFF;
  font-size: 6rem;
  font-family: "Roboto", sans-serif;
  letter-spacing: 1rem;
  margin: 0 0 5rem 5rem;
  padding: 0 1rem;
  border-left: 1rem solid #000;
}
@media (min-width: 768px) {
  .footer__titulo {
    font-size: 8rem;
  }
}
.footer__info {
  padding: 2rem 0;
  background-color: #c30606;
  height: 12rem;
  position: relative;
}
.footer__info:before {
  content: "";
  background-color: #000;
  height: 12rem;
  position: absolute;
  width: 100%;
  transform: skew(-80deg);
  left: 35rem;
  top: 0;
}
@media (min-width: 768px) {
  .footer__info:before {
    left: 50rem;
  }
}
@media (min-width: 1024px) {
  .footer__info:before {
    content: "";
    left: 75rem;
  }
}
.footer__cajas {
  background-color: #333333;
  padding: 2rem;
}
@media (min-width: 768px) {
  .footer__cajas {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__bloque {
  grid-column: 1/3;
  padding: 2rem;
}
.footer__ubicacion {
  grid-column: 3/5;
}
.footer__ubicacion-imagen {
  border-radius: 2rem;
}
.footer__lista {
  margin-bottom: 2rem;
}
.footer__iconos {
  margin-bottom: 1rem;
  font-size: 1.8rem;
  color: #FFFFFF;
}
.footer__iconos:last-of-type {
  margin-bottom: 0;
}
.footer__iconos-link {
  color: #FFFFFF;
}
.footer__icono {
  font-size: 2rem;
  margin-right: 1rem;
  color: #FFFFFF;
}
.footer__redes-lista {
  padding: 4rem 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.footer__copyright {
  font-size: 1.5rem;
  margin-bottom: 0;
  padding: 1rem;
  text-align: center;
  color: #c30606;
  margin: 0;
}